.section-padding{
  padding-top:50px;
  padding-bottom:35px;
}
.easy-steps{
  margin-top:25px;
  margin-bottom:35px;
}

#landing-page .presentation-login .row{
  height: auto;
  
}
.catalogue{
  height: 600px;
}
.catalogue img{
    object-fit:cover;
    width: 100%;
    height: 500px;
}
.catalogue-ad{
  background-image:linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.0)), url("https://images.unsplash.com/photo-1517191434949-5e90cd67d2b6?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1470&q=80");
  background-repeat: no-repeat;
  background-position:center center;
  background-size: cover;
 
}


.carousel-caption{
  width:100%;
  height:100%;
  left:0;
  right:0;
  bottom:0;
  background-color:rgba(0,0,0,0.4);
}

.carousel-description{
  padding-left: 100px;
  padding-right: 100px 
}

.main-title{
  color:white;
  font-size: 7rem;
} 
@media screen and (max-width:768px){
  .main-title{
    color:white;
    font-size: 4rem;
  } 
}
