#register-page{
  background-image: url("https://bergamotte.imgix.net/z049fzgxm50nzio6q6ri06wzq7op?ixlib=rails-4.2.0&auto=format%2Ccompress&fit=crop&q=65&ar=1%3A1&w=560");
  background-repeat:no-repeat;
  background-size: cover;
  width: 100%;

}

.spinner {
  background-image:linear-gradient(rgba(0, 0, 0, 0.5),rgba(0, 0, 0, 0.5)), url("https://bergamotte.imgix.net/z049fzgxm50nzio6q6ri06wzq7op?ixlib=rails-4.2.0&auto=format%2Ccompress&fit=crop&q=65&ar=1%3A1&w=560");
  background-repeat:no-repeat;
  background-size: cover;
  height: 100vh;
  opacity: 0.9;
}


